import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            list: [],
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
            pageShow: false,
			page:1,
			haveMore: true,
			totalNum:0,
			search: '',
			defaultImg: 'this.src="' + require('../../assets/patient/pat_1_2.png') + '"'
        }
    },
    created(){
		this.getReferralRecord(false)
    },
    mounted(){
		 this.getMore()
    },
    beforeDestroy(){
		window.onscroll = null
    },
    methods:{
		getReferralRecord(h){
			if(this.haveMore){
				this.$post('/PlatFormAPI/DoctorPatient/QueryDoctorPatientPage',{
					DrId: this.DrId,
					DrName: "",
					PatientId: -1,
					PatientName: this.search,
					RelationStatus: 1,
					IsDrKey: -1,
					IsPatientMain: -1,
					PageIndex: this.page,
					PageSize: this.size,
				}).then(o => {
					this.pageShow = true
					if (!o.ReturnData || o.ReturnData.length < 10){
					    this.haveMore = false
					}
					else {
					    this.haveMore = true
					}
					
					if (h){
					    o.ReturnData.forEach(v => {
					        this.list.push(v)
					    })
					}
					else {
					    this.list = o.ReturnData
					}
					this.totalNum = o.Total
				})
			}
		},
		getMore(){
		    const el = this.$refs.el
		    let timer = null
		    window.onscroll = () => {
		        clearTimeout(timer)
		        timer = setTimeout(() => {
		            const top = document.documentElement.scrollTop
		                        || window.pageYOffset
		                        || document.body.scrollTop,
		                    h = el.scrollHeight,
		                    wh = window.innerHeight
		
		            if (h - top - wh < 100 && this.haveMore){
		                this.page += 1
		                this.getReferralRecord(true)
		            }
		        }, 100)
		    }
		},
		 searchFor(){
		 	if(this.inputFlag) {
				return
			}
		    this.haveMore = true
		    this.page = 1
		    this.getReferralRecord(false)
		},
		keepInfo(i){
			this.$store.dispatch('pushpatient', i)
			this.$router.push(`/archives`)
		},
		choosePatient(i){
			this.$router.push({
				path: "/chooseDr",
				query: {
					PatientId: i.PatientId,
					PatientName: i.PatientName,
					SpecialistHosGroupId:this.$route.query.SpecialistHosGroupId,
					HosGroupName:this.$route.query.HosGroupName
				}
			})
		},
    }
}



